function copyToClipboard(text: string) {
  navigator.clipboard.writeText(text).then(() => {
    window.toastr.success('');
  });
}

function generateStructure() {
  if (!varName.value.trim()) {
    window.toastr.error('');
    return;
  }

  const result: any = {
    $meta: {
      extensible: false,
      strictSet: true,
    },
  };

  result[varName.value] = parseSubFields();
  generatedStructure.value = JSON.stringify(result, null, 2);
}

function parseSubFields(): any {
  const lines = subFields.value.split('\n').filter(l => l.trim());
  const obj: any = {};

  lines.forEach(line => {
    const parts = line.split(':').map(s => s.trim());
    const name = parts[0];
    const type = parts[1] || 'string';
    const desc = parts[2] || '...';

    if (!name) return;

    if (type === 'number') {
      obj[name] = [0, desc];
    } else if (type === 'string') {
      obj[name] = ['', desc];
    } else if (type === 'array') {
      obj[name] = [[], desc];
    } else if (type === 'object') {
      obj[name] = [{}, desc];
    } else {
      obj[name] = [null, desc];
    }
  });

  return obj;
}

function copyGenerated() {
  copyToClipboard(generatedStructure.value);
}

