    const apiUrl = normalizeApiEndpoint(settings.value.api_endpoint);
    const response = await fetch(apiUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${settings.value.api_key}`,
      },
      body: JSON.stringify({
        model: settings.value.model,
        messages: [
          { role: 'system', content: systemPrompt },
          { role: 'user', content: `${aiStructurePrompt.value}\n\n [InitVar] JSON` },
        ],
        temperature: settings.value.temperature || 0.7,
        max_tokens: settings.value.max_tokens || 4000,
      }),
    });

    // 
    const responseText = await response.text();

    if (!response.ok) {
      // 
      let errorMessage = `API : ${response.status}`;
      try {
        const errorData = JSON.parse(responseText);
        if (errorData.error) {
          errorMessage = `${errorMessage} - ${errorData.error.message || JSON.stringify(errorData.error)}`;
        } else if (errorData.message) {
          errorMessage = `${errorMessage} - ${errorData.message}`;
        } else {
          errorMessage = `${errorMessage} - ${JSON.stringify(errorData)}`;
        }
      } catch (e) {
        errorMessage = `${errorMessage} - ${responseText.slice(0, 200)}`;
      }
      console.error('API :', errorMessage);
      console.error(' URL:', settings.value.api_endpoint);
      console.error(
        ':',
        JSON.stringify(
          {
            model: settings.value.model,
            messages: [
              { role: 'system', content: systemPrompt.substring(0, 100) + '...' },
