//  -  localStorage
const deleteSummary = (index: number) => {
  if (confirm('')) {
    try {
      const chat_id = getChatIdSafe();
      if (!chat_id) {
        window.toastr.error('');
        return;
      }

      summary_history.value.splice(index, 1);

      //  localStorageID
      const scriptId = getScriptIdSafe();
      const storageKey = `${scriptId}_summary_history_${chat_id}`;
      localStorage.setItem(storageKey, JSON.stringify(summary_history.value));
      console.log('  localStorage');

      window.toastr.success('');
    } catch (error) {
      console.error(':', error);
      window.toastr.error('');
    }
  }
};

// 
const createSummaryWorldbook = async () => {
  try {
    // 
    let characterName = '';
    try {
      const currentCharacter = getCharData('current');
      if (currentCharacter && currentCharacter.name) {
        characterName = currentCharacter.name;
        // 
        characterName = characterName.replace(/[<>:"/\\|?*]/g, '_').trim();
      }
    } catch (charError) {
      console.warn(':', charError);
    }

    // 
    const dateStr = new Date().toISOString().slice(0, 10);
    const worldbookName = `_${characterName}_${dateStr}`;

    console.log(':', worldbookName);

    const existingWorldbooks = getWorldbookNames();
    if (existingWorldbooks.includes(worldbookName)) {
      window.toastr.warning(` "${worldbookName}" `);
      return;
    }

    await createWorldbook(worldbookName, []);

    try {
      await rebindChatWorldbook('current', worldbookName);
      window.toastr.success(` "${worldbookName}" `);
    } catch (bindError) {
      console.warn(':', bindError);
      window.toastr.success(` "${worldbookName}"`);
    }
  } catch (error) {
    console.error(':', error);
    window.toastr.error(': ' + (error as Error).message);
  }
};

// 
const bindToWorldbook = async (content: string, summaryIndex: number) => {
  try {
    const worldbookNames = getWorldbookNames();
    if (worldbookNames.length === 0) {
      window.toastr.warning('');
      return;
    }

    const selectedWorldbook =
      worldbookNames.length === 1 ? worldbookNames[0] : await promptForWorldbook(worldbookNames);
    if (!selectedWorldbook) {
      return;
    }

    const entryName = `_${summaryIndex + 1}_${new Date().toLocaleDateString()}`;
    const newEntry = {
      name: entryName,
      enabled: true,
      strategy: {
        type: 'constant' as const,
        keys: [],
        keys_secondary: { logic: 'and_any' as const, keys: [] },
        scan_depth: 'same_as_global' as const,
      },
      position: {
        type: 'before_character_definition' as const,
        role: 'system' as const,
        depth: 1,
      },
      content: content,
      comment: ` - : ${summary_history.value[summaryIndex]?.start_id || '?'} - ${summary_history.value[summaryIndex]?.end_id || '?'}`,
      insertion_order: 0,
      case_sensitive: false,
      name_is_regex: false,
      keys_are_regex: false,
      selective_logic: 'and',
      secondary_keys_logic: 'and',
      activation_threshold: 0,
      disabled: false,
      order: 0,
      group: '',
      local_id: 0,
      depth_entries: [],
      depth_entries_all: false,
      search_range: 0,
      force_activation: false,
      disable: false,
      exclude_recursion: false,
      hidden: false,
      priority: 0,
      comment_is_regex: false,
      content_is_regex: false,
      secondary_keys_are_regex: false,
      keys_secondary_are_regex: false,
      uid: Date.now(),
    };

    await createWorldbookEntries(selectedWorldbook, [newEntry]);
    window.toastr.success(`: ${selectedWorldbook}`);
  } catch (error) {
    console.error(':', error);
    window.toastr.error(': ' + (error as Error).message);
  }
};

// 
const promptForWorldbook = async (worldbookNames: string[]): Promise<string | null> => {
  return new Promise<string | null>(resolve => {
    const dialog = $(`
      <div style="
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: #2a2a2a;
        border: 1px solid #3a3a3a;
        border-radius: 8px;
        padding: 20px;
        z-index: 1000000;
        min-width: 300px;
      ">
        <h3 style="margin: 0 0 15px 0; color: #e0e0e0;"></h3>
        <div style="margin-bottom: 15px;">
          ${worldbookNames
            .map(
              name => `
            <label style="display: block; margin: 5px 0; color: #e0e0e0;">
              <input type="radio" name="worldbook" value="${name}" style="margin-right: 8px;">
              ${name}
            </label>
          `,
            )
            .join('')}
        </div>
        <div style="text-align: right;">
          <button id="cancelWorldbook" style="
            background: #666;
            color: white;
            border: none;
            padding: 8px 16px;
            border-radius: 4px;
            margin-right: 10px;
            cursor: pointer;
          "></button>
          <button id="confirmWorldbook" style="
            background: #4a9eff;
            color: white;
            border: none;
            padding: 8px 16px;
            border-radius: 4px;
            cursor: pointer;
          "></button>
        </div>
      </div>
    `);

    const overlay = $(`
      <div style="
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.5);
        z-index: 999999;
      "></div>
    `);

    $('body').append(overlay).append(dialog);

    $('#cancelWorldbook').on('click', () => {
      overlay.remove();
      dialog.remove();
      resolve(null);
    });

    $('#confirmWorldbook').on('click', () => {
      const selected = $('input[name="worldbook"]:checked').val() as string;
      overlay.remove();
      dialog.remove();
      resolve(selected || null);
    });

    $('input[name="worldbook"]:first').prop('checked', true);
  });
};

