  const character = getCurrentCharacter();
  if (!character) {
    toastr.warning('');
    return;
  }

  // AIHTML
  if (uiConfig.value.customHtml && uiConfig.value.customHtml.trim()) {
    generatedCode.value = uiConfig.value.customHtml;
    showCodeDialog.value = true;
    return;
  }

  // HTML
  toastr.warning('"AI"HTML');
}

// 
async function copyCode() {
  const success = await copyToClipboard(generatedCode.value, ' ');
  if (success) {
    copyButtonText.value = ' ';
    setTimeout(() => {
      copyButtonText.value = '';
    }, 2000);
  }
}

// 
function updatePreview() {
  if (greetings.value.length === 0) {
    previewHtml.value = '';
    return;
  }

  // AIHTML
  if (uiConfig.value.customHtml && uiConfig.value.customHtml.trim()) {
    console.log(' AIHTML');

    try {
      // HTML
      const cardsHtml = greetings.value
        .map((g, index) => {
          const icon = g.icon || '';
          const title = g.title || (index === 0 ? '' : ` ${index}`);
          const desc = g.description || '';
          const badge = index === 0 ? '' : String(index).padStart(2, '0');

          return `
            <div class="scene-card" onclick="switchGreeting(${index})">
              <span class="card-badge">${badge}</span>
              <div class="card-icon">${icon}</div>
              <h3 class="card-title">${title}</h3>
              <p class="card-desc">${desc}</p>
            </div>
          `.trim();
        })
        .join('\n          ');

      // HTML.scenes-grid
      let html = uiConfig.value.customHtml;
      html = html.replace(
        /<div class="scenes-grid">[\s\S]*?<\/div>/,
        `<div class="scenes-grid">\n          ${cardsHtml}\n        </div>`,
      );

      previewHtml.value = html;
      console.log(' ');
    } catch (error) {
      console.error(' :', error);
      previewHtml.value = '';
    }
    return;
  }

  // HTML
  previewHtml.value = '';
}

// 
function closeCodeDialog() {
  showCodeDialog.value = false;
}

// 
function openPreviewInNewWindow() {
  if (!previewHtml.value) {
    toastr.error('');
    return;
  }

  const character = getCurrentCharacter();
  const windowTitle = character ? `${character.name} - ` : '';

  // 
  const newWindow = window.open('', '_blank', 'width=1200,height=800,menubar=no,toolbar=no,location=no,status=no');
  if (newWindow) {
    newWindow.document.write(previewHtml.value);
    newWindow.document.close();
    newWindow.document.title = windowTitle;
    toastr.success('');
  } else {
    toastr.error('');
  }
}

// STScript JSON
async function downloadAsJson() {
  try {
    await generateFrontendCode();

    if (!generatedCode.value) {
      toastr.error('');
      return;
    }

    const character = getCurrentCharacter();
    const characterName = character?.name || '';

    // UUID
    const uuid = crypto.randomUUID();

    // STScript JSON
    const stscriptJson = {
      id: uuid,
      scriptName: `${characterName}-`,
      disabled: false,
      runOnEdit: true,
      findRegex: '//g',
      replaceString: '```html\n' + generatedCode.value + '\n```',
      trimStrings: [],
      placement: [1, 2],
      substituteRegex: 0,
      minDepth: null,
      maxDepth: null,
      markdownOnly: true,
      promptOnly: false,
    };

    // JSON
    const jsonString = JSON.stringify(stscriptJson, null, 4);

    // Blob
    const blob = new Blob([jsonString], { type: 'application/json' });

    // 
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = `${characterName}-.json`;

    // 
    document.body.appendChild(link);
    link.click();

    // 
    document.body.removeChild(link);
    URL.revokeObjectURL(url);

    toastr.success('JSON');
  } catch (error) {
    console.error(':', error);
    toastr.error(': ' + (error as Error).message);
  }
}

// 
onMounted(() => {
  loadConfig();
});
</script>

