// AI  XML 
async function parseXmlWithAI() {
  if (!xmlInput.value.trim()) {
    toastr.warning(' XML ');
    return;
  }

  //  API 
  if (!settings.value.api_endpoint || !settings.value.api_key) {
    toastr.error(' API " " API ', '', {
      timeOut: 0,
      extendedTimeOut: 0,
      closeButton: true,
    });
    return;
  }

  // 
  const taskId = taskStore.createTask('ui_generate', 'AIXML');

  // 
  originalXmlInput.value = xmlInput.value;

  isParsingXml.value = true;
  showProgress.value = true;

  try {
    progressDialogRef.value?.setProgress(10);
    progressDialogRef.value?.setMessage(' XML...');
    progressDialogRef.value?.addDetail(`XML : ${xmlInput.value.length} `);
    taskStore.updateTaskProgress(taskId, 10, 'XML');
    const systemPrompt = ` XML  XML 

1. **** XML  <i><2i><><> 
2. ****
   -  <i><2i><3i> 
   -  <><>
3. **** namelabelicon

** JSON **

[
  { "name": "1", "label": "1", "icon": "fa-solid fa-xxx" },
  { "name": "2", "label": "2", "icon": "" }
]

****
- name  date, location, status
- label  
- icon  Font Awesome 
- **** JSON 
- **** JSON


<state_bar>
<i></i>
<></>
<></>
</state_bar>


[
  { "name": "content", "label": "", "icon": "" },
  { "name": "date", "label": "", "icon": "fa-solid fa-calendar" },
  { "name": "physical_status", "label": "", "icon": "fa-solid fa-heart-pulse" }
]`;

    const userPrompt = ` XML 

\`\`\`xml
${xmlInput.value.trim()}
\`\`\`

 JSON `;

    console.log('  AI  XML...');
    console.log('  API:', settings.value.api_endpoint);
    console.log(' :', settings.value.model);

    progressDialogRef.value?.setProgress(20);
    progressDialogRef.value?.setMessage(' XML  AI ...');
    taskStore.updateTaskProgress(taskId, 20, 'XMLAI');

    //  API 
    const apiUrl = normalizeApiEndpoint(settings.value.api_endpoint);
    console.log(' :', apiUrl);

    //  AI API
    progressDialogRef.value?.setProgress(30);
    progressDialogRef.value?.setMessage(' AI  XML ...');
    taskStore.updateTaskProgress(taskId, 40, `AI (${settings.value.model})`);

    const response = await fetch(apiUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${settings.value.api_key}`,
      },
      body: JSON.stringify({
        model: settings.value.model,
        messages: [
          {
            role: 'system',
            content: systemPrompt,
          },
          {
            role: 'user',
            content: userPrompt,
          },
        ],
        max_tokens: 2000,
        temperature: 0.7,
      }),
    });

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(`API: ${response.status} ${response.statusText}\n${errorText}`);
    }

    const result = await response.json();
    console.log(' AI :', result);

    if (!result.choices || !result.choices[0] || !result.choices[0].message) {
      throw new Error('AI');
    }

    const aiResponse = result.choices[0].message.content;
    console.log(' AI :', aiResponse);

    // =====  =====
    if (!aiResponse || typeof aiResponse !== 'string') {
      throw new Error('AI ');
    }

    //  JSON
    let jsonText = aiResponse.trim();

    //  markdown 
    jsonText = jsonText.replace(/^```(?:json)?\s*\n?/i, '').replace(/\n?```\s*$/i, '');

    //  JSON  AI 
    const jsonMatch = jsonText.match(/\[\s*{[\s\S]*}\s*\]/);
    if (jsonMatch) {
      jsonText = jsonMatch[0];
    }

    //  JSON
    let parsedFields: any;
    try {
      parsedFields = JSON.parse(jsonText);
    } catch (parseError: any) {
      throw new Error(
        `AI  JSON \n\n AI \n\n200:\n${result.slice(0, 200)}`,
      );
    }

    if (!Array.isArray(parsedFields)) {
      throw new Error('AI ');
    }

    // 
    const validFields = parsedFields.filter((field: any) => {
      return field.name && field.label;
