// 
function generateFromFields() {
  const fields = config.value.fields;

  //  findRegex
  const totalFields = fields.length;

  if (totalFields === 0) {
    config.value.findRegex = '<-CHARACTER_STATUS->';
    console.log(' ');
    return;
  }

  if (totalFields <= 4) {
    // 4
    config.value.findRegex = `<-CHARACTER_STATUS->[\\r\\n]*\\|${fields.map(() => '([^|]+)').join('\\|')}\\|[\\r\\n]*`;
    console.log('  findRegex ():', config.value.findRegex);
    console.log(' :', totalFields);
  } else {
    // 4
    // 4
    const perLine = totalFields <= 8 ? Math.ceil(totalFields / 2) : 4;

    // 
    const firstHalf = Math.min(Math.ceil(totalFields / 2), totalFields <= 12 ? 4 : Math.ceil(totalFields / 3));

    const lines: string[] = [];

    //  + 
    lines.push('<-ENVIRONMENT_DATA->[\\r\\n]*');
    for (let i = 0; i < firstHalf; i += perLine) {
      const count = Math.min(perLine, firstHalf - i);
      lines.push(
        '\\|' +
          fields
            .slice(i, i + count)
            .map(() => '([^|]+)')
            .join('\\|') +
          '\\|[\\r\\n]*',
      );
    }

    //  + perLine
    lines.push('<-CHARACTER_STATUS->[\\r\\n]*');
    for (let i = firstHalf; i < totalFields; i += perLine) {
      const count = Math.min(perLine, totalFields - i);
      lines.push(
        '\\|' +
          fields
            .slice(i, i + count)
            .map(() => '([^|]+)')
            .join('\\|') +
          '\\|[\\r\\n]*',
      );
    }

    config.value.findRegex = lines.join('');

    console.log('  findRegex ():', config.value.findRegex);
    console.log(' :', totalFields);
    console.log(
      ' :',
      `: ${firstHalf}, : ${totalFields - firstHalf}, : ${perLine}`,
    );
  }

  //  HTML
  const fieldsHtml = fields
    .map((f, i) => {
      const iconPart = f.icon ? `<i class="${f.icon}"></i> ` : '';
      return `        <div class="status-field">
          <div class="field-label">${iconPart}${f.label || '' + (i + 1)}</div>
          <div class="field-value">$${i + 1}</div>
        </div>`;
    })
    .join('\n');

  const htmlContent = `<details>
<summary> ${config.value.name} </summary>
<div class="status-card">
    <div class="status-header">
        <span></span>
        <span>${config.value.name}</span>
    </div>
    <div class="status-content">
        <div class="status-grid">
${fieldsHtml}
        </div>
    </div>
</div>
</details>`;

  //  CSS
  const cssContent = `.status-card {
    font-family: 'Microsoft YaHei', sans-serif;
    background: linear-gradient(145deg, #1e293b, #334155);
    border-radius: 14px;
    margin: 16px 0;
    box-shadow: 0 8px 25px rgba(15, 23, 42, 0.4);
    overflow: hidden;
    border: 1px solid rgba(148, 163, 184, 0.2);
    max-width: 600px;
}

.status-header {
    background: linear-gradient(135deg, #0f172a, #1e293b);
    color: #e2e8f0;
    padding: 12px 20px;
    font-size: 15px;
    font-weight: 600;
    display: flex;
    align-items: center;
    gap: 8px;
    border-bottom: 1px solid rgba(148, 163, 184, 0.15);
}

.status-content {
    padding: 20px;
    background: rgba(30, 41, 59, 0.5);
}

.status-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
}

.status-field {
    background: rgba(30, 41, 59, 0.8);
    padding: 16px;
    border-radius: 10px;
    border-left: 3px solid #6366f1;
}

.field-label {
    font-size: 11px;
    color: #94a3b8;
    margin-bottom: 8px;
    font-weight: 600;
    text-transform: uppercase;
}

.field-value {
    font-size: 14px;
    color: #e2e8f0;
    line-height: 1.5;
    font-weight: 600;
}`;

  //  JS - 
  const jsContent = `// 
(function() {
  // 
