const handle_summarize = async () => {
  let taskId: string | null = null;
  try {
    if (is_summarizing.value) return;

    console.log('...');
    is_summarizing.value = true;
    saveGenerationStatus();

    // 
    taskId = taskStore.addTask({
      title: '',
      description: ` ${settings.value.start_message_id} - ${settings.value.end_message_id}`,
      category: '',
    });

    //  API 
    if (!settings.value.api_endpoint || !settings.value.api_key) {
      window.toastr.warning(' API  API Key');
      return;
    }

    // 
    if (settings.value.start_message_id === undefined || settings.value.end_message_id === undefined) {
      window.toastr.warning('');
      return;
    }

    // 
    if (settings.value.start_message_id < 0 || settings.value.end_message_id < settings.value.start_message_id) {
      window.toastr.warning('');
      return;
    }

    console.log(`: ${settings.value.start_message_id} - ${settings.value.end_message_id}`);

    // 
    showProgress.value = true;
    progressDialogRef.value?.setProgress(10);
    progressDialogRef.value?.setMessage('...');
    progressDialogRef.value?.addDetail(
      `: ${settings.value.start_message_id} - ${settings.value.end_message_id}`,
    );

    progressDialogRef.value?.setProgress(30);
    progressDialogRef.value?.setMessage(' AI ...');
    progressDialogRef.value?.addDetail(' 10-30 ');

    // 
    const { summarizeMessages } = await import('../');
    const summary = await summarizeMessages(settings.value.start_message_id, settings.value.end_message_id);

    progressDialogRef.value?.setProgress(90);
    progressDialogRef.value?.setMessage('...');

    console.log(':', summary);

    //  -  localStorage
    const scriptId = getScriptIdSafe();
    if (scriptId) {
      const storageKey = `${scriptId}_last_summary`;
      localStorage.setItem(storageKey, JSON.stringify({ last_summary: summary }));
      console.log('  localStorage');
    }

    // 
    summaryHistoryStore.addSummary(
      settings.value.start_message_id ?? 0,
      settings.value.end_message_id ?? settings.value.start_message_id ?? 0,
      summary,
    );

    // 
    window.dispatchEvent(new CustomEvent('summary-history-updated'));

    progressDialogRef.value?.setProgress(100);
    progressDialogRef.value?.setMessage(' ');
    progressDialogRef.value?.addDetail('');

    setTimeout(() => {
      showProgress.value = false;
      window.toastr.success('');
      // 
      if (taskId) {
        taskStore.completeTask(taskId);
      }
    }, 800);
  } catch (error) {
    console.error(':', error);
    showProgress.value = false;
    window.toastr.error(': ' + (error as Error).message);
    // 
    if (taskId) {
      taskStore.failTask(taskId, (error as Error).message);
    }
  } finally {
    is_summarizing.value = false;
    saveGenerationStatus();
  }
};

const handle_generate_table = async () => {
  let taskId: string | null = null;
  try {
    if (is_generating_table.value) return;

    console.log('...');
    is_generating_table.value = true;
    saveGenerationStatus();

    // 
    taskId = taskStore.addTask({
      title: '',
      description: ` ${settings.value.table_start_message_id} - ${settings.value.table_end_message_id} `,
      category: '',
    });

    //  API 
    if (!settings.value.api_endpoint || !settings.value.api_key) {
      window.toastr.warning(' API  API Key');
      return;
    }

    // 
    showProgress.value = true;
    progressDialogRef.value?.setProgress(5);
    progressDialogRef.value?.setMessage('...');

    // 
    if (!settings.value.table_start_message_id || !settings.value.table_end_message_id) {
      window.toastr.warning('');
      return;
    }

    // 
    let headersInput: string | null = null;

    if (currentTemplate.value.headers.trim()) {
      // 
      const useTemplate = confirm(
        `"${currentTemplate.value.name}"\n${currentTemplate.value.headers}`,
      );
      if (useTemplate) {
        headersInput = currentTemplate.value.headers;
      }
    }

    if (!headersInput) {
      // 
      headersInput = window.prompt(',,,');
    }

    if (!headersInput || !headersInput.trim()) {
      window.toastr.warning('');
      return;
    }

    const headers: string[] = headersInput
      .split(',')
      .map((h: string) => h.trim())
      .filter((h: string) => h);

    if (headers.length === 0) {
      window.toastr.warning('');
      return;
    }

    progressDialogRef.value?.setProgress(15);
    progressDialogRef.value?.setMessage('...');
    progressDialogRef.value?.addDetail(
      `: ${settings.value.table_start_message_id} - ${settings.value.table_end_message_id}`,
    );

    // 
    let chatMessages;
    try {
      const messageRange = `${settings.value.table_start_message_id}-${settings.value.table_end_message_id}`;
      console.log(':', messageRange);

      //  TavernHelper.getChatMessages()
      if (
        typeof (window as any).TavernHelper !== 'undefined' &&
        typeof (window as any).TavernHelper.getChatMessages === 'function'
      ) {
        chatMessages = (window as any).TavernHelper.getChatMessages(messageRange);
        console.log('  TavernHelper.getChatMessages() :', chatMessages.length);
      } else {
        throw new Error('TavernHelper.getChatMessages ');
      }

      progressDialogRef.value?.addDetail(` ${chatMessages.length} `);
    } catch (error) {
      console.error(':', error);
      showProgress.value = false;
      window.toastr.error(': ' + (error as Error).message);
      return;
    }

    if (!chatMessages || chatMessages.length === 0) {
      window.toastr.warning('');
      return;
    }

    // AI
    const messagesText = chatMessages
      .map((msg, idx) => {
        const role = msg.role === 'user' ? '' : 'AI';
        return `[${idx + 1}] ${role}: ${msg.message}`;
      })
      .join('\n\n');

    const systemPrompt = `

# 
JSON

# 
1. ****
2. ****
3. ****
4. ****""

# 
JSON
{
  "data": [
    ["1", "2", "3"],
    ["4", "5", "6"]
  ]
}

# 
 
 markdown\`\`\`json
 
 

`;

    const userPrompt = `

****${headers.join(', ')}
${headers.length}${headers.length}

****
${messagesText}

---

****
- [${headers.map(h => `"${h}"`).join(', ')}]
- """-"
- 
- JSON

JSON`;

    console.log('AI...');
    console.log(' System Prompt:', systemPrompt);
    console.log(' User Prompt:', userPrompt.slice(0, 500) + '...');

    progressDialogRef.value?.setProgress(30);
    progressDialogRef.value?.setMessage(' AI ...');
    progressDialogRef.value?.addDetail(`: ${headers.join(', ')}`);

    // 
    const { normalizeApiEndpoint, filterApiParams } = await import('../settings');
    const apiUrl = normalizeApiEndpoint(settings.value.api_endpoint);

    progressDialogRef.value?.setProgress(40);
    progressDialogRef.value?.setMessage(' AI ...');
    progressDialogRef.value?.addDetail(' 10-30 ');

    const requestParams = {
      model: settings.value.model,
      messages: [
        {
          role: 'system',
          content: systemPrompt,
        },
        {
          role: 'user',
          content: userPrompt,
        },
      ],
      max_tokens: settings.value.max_tokens,
      temperature: 0.3, // 
      top_p: settings.value.top_p,
      presence_penalty: settings.value.presence_penalty,
      frequency_penalty: settings.value.frequency_penalty,
    };

    //  API 
    const filteredParams = filterApiParams(requestParams, settings.value.api_endpoint);

    // AI
    const response = await fetch(apiUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${settings.value.api_key}`,
      },
      body: JSON.stringify(filteredParams),
    });

    if (!response.ok) {
      const errorText = await response.text();
      showProgress.value = false;
      console.error(' API:', response.status, response.statusText);
      console.error(':', errorText);
      window.toastr.error(
        `API\n\n: ${response.status}\n: ${response.statusText}\n\n\n1. API\n2. API Key\n3. `,
        '',
        { timeOut: 10000 },
      );
      return;
    }

    progressDialogRef.value?.setProgress(70);
    progressDialogRef.value?.setMessage(' AI ...');

    const result = await response.json();
    console.log('AI:', result);

    if (!result.choices || !result.choices[0] || !result.choices[0].message) {
      showProgress.value = false;
      console.error(' AI:', result);
      window.toastr.error('AIAPIOpenAI', '', { timeOut: 8000 });
      return;
    }

    const aiResponse = result.choices[0].message.content;
    console.log('AI:', aiResponse);

    progressDialogRef.value?.setProgress(85);
    progressDialogRef.value?.setMessage('...');

    // AIJSON
    let aiTableData;
    try {
      // APIJSON
      if (
        aiResponse.includes('API') ||
        aiResponse.includes('') ||
        aiResponse.includes('') ||
        aiResponse.includes('error') ||
        (!aiResponse.includes('{') && !aiResponse.includes('['))
      ) {
        // JSON
        showProgress.value = false;
        window.toastr.error(`APIAPI\n\n\n${aiResponse.slice(0, 200)}`, '', {
          timeOut: 10000,
        });
        console.error(' API:', aiResponse);
        return;
      }

      // JSONmarkdown
      let jsonText = aiResponse.trim();

      // markdown
      jsonText = jsonText
        .replace(/^```json\s*/i, '')
        .replace(/^```\s*/, '')
        .replace(/```\s*$/, '');

      // JSON
      const jsonMatch = jsonText.match(/\{[\s\S]*\}/);
      if (jsonMatch) {
        aiTableData = JSON.parse(jsonMatch[0]);
      } else {
        throw new Error('JSON');
      }
    } catch (parseError) {
      console.error('AI:', parseError);
      console.log('AI:', aiResponse);
      showProgress.value = false;
      window.toastr.error(`AI\n\n\n${aiResponse.slice(0, 200)}`, '', { timeOut: 8000 });
      return;
    }

    // 
    if (!aiTableData.data || !Array.isArray(aiTableData.data)) {
      window.toastr.error('AIdata');
      return;
    }

    // 
    for (let i = 0; i < aiTableData.data.length; i++) {
      if (!Array.isArray(aiTableData.data[i])) {
        window.toastr.error(`${i + 1}`);
        return;
      }
      if (aiTableData.data[i].length !== headers.length) {
        window.toastr.error(`${i + 1}${headers.length}${aiTableData.data[i].length}`);
        console.log(`:`, headers);
        console.log(`:`, aiTableData.data[i]);
        return;
      }
    }

    // headers
    const tableData = {
      headers: headers,
      data: aiTableData.data,
    };

    //  -  localStorageID
    const chat_id = getChatIdSafe();
    if (chat_id) {
      const scriptId = getScriptIdSafe();
      const storageKey = `${scriptId}_table_history_${chat_id}`;

      //  localStorage 
      let table_history = [];
      const savedData = localStorage.getItem(storageKey);
      if (savedData) {
        try {
          table_history = JSON.parse(savedData);
        } catch (e) {
          console.error(':', e);
          table_history = [];
        }
      }

      // 
      table_history.push({
        start_id: settings.value.table_start_message_id,
        end_id: settings.value.table_end_message_id,
        headers: tableData.headers,
        data: tableData.data,
      });

      //  localStorage
      localStorage.setItem(storageKey, JSON.stringify(table_history));
      console.log('  localStoragechat_id:', chat_id);

      progressDialogRef.value?.setProgress(100);
      progressDialogRef.value?.setMessage(' ');
      progressDialogRef.value?.addDetail(` ${tableData.data.length} `);

      setTimeout(() => {
        showProgress.value = false;
        window.toastr.success(`${tableData.data.length}`);
        // 
        if (taskId) {
          taskStore.completeTask(taskId);
        }
      }, 800);

      console.log(':', table_history);
    } else {
      showProgress.value = false;
      window.toastr.warning('ID');
    }
  } catch (error) {
    console.error(':', error);
    showProgress.value = false;
    window.toastr.error(': ' + (error as Error).message);
    // 
    if (taskId) {
      taskStore.failTask(taskId, (error as Error).message);
    }
  } finally {
    is_generating_table.value = false;
    saveGenerationStatus();
  }
};

const handle_create_table = () => {
  try {
    // 
    if (!settings.value.table_start_message_id || !settings.value.table_end_message_id) {
      window.toastr.warning('');
      return;
    }

    // 
    const headersInput: string | null = window.prompt(',,,');
    if (!headersInput || !headersInput.trim()) {
      window.toastr.warning('');
      return;
    }

    // 
    const headers: string[] = headersInput
      .split(',')
      .map((h: string) => h.trim())
      .filter((h: string) => h);

    if (headers.length === 0) {
      window.toastr.warning('');
      return;
    }

    // 
    const emptyTableData = {
      start_id: settings.value.table_start_message_id,
      end_id: settings.value.table_end_message_id,
      headers: headers,
      data: [], // 
    };

    //  -  localStorageID
    const chat_id = getChatIdSafe();
    if (chat_id) {
      const scriptId = getScriptIdSafe();
      const storageKey = `${scriptId}_table_history_${chat_id}`;

      //  localStorage 
      let table_history = [];
      const savedData = localStorage.getItem(storageKey);
      if (savedData) {
        try {
          table_history = JSON.parse(savedData);
        } catch (e) {
          console.error(':', e);
          table_history = [];
        }
      }

      // 
      table_history.push(emptyTableData);

      //  localStorage
      localStorage.setItem(storageKey, JSON.stringify(table_history));
      console.log('  localStoragechat_id:', chat_id);

      window.toastr.success(`${headers.join(', ')}`);
      console.log(':', emptyTableData);
    } else {
      window.toastr.warning('ID');
    }
  } catch (error) {
    console.error(':', error);
    window.toastr.error(': ' + (error as Error).message);
  }
};

const handle_hide_messages = async () => {
  try {
    console.log('...');

    if (!hide_range.value.trim()) {
      window.toastr.warning('');
      return;
    }

    // 
    const ranges = hide_range.value.split(',').map(range => range.trim());
    const messageIds: number[] = [];

    for (const range of ranges) {
      if (range.includes('-')) {
        // 1-10
        const [start, end] = range.split('-').map(Number);
        if (start && end && start <= end) {
          for (let i = start; i <= end; i++) {
            messageIds.push(i);
          }
        }
      } else {
        // 5
        const id = Number(range);
        if (id) {
          messageIds.push(id);
        }
      }
    }

    if (messageIds.length === 0) {
      window.toastr.warning('');
      return;
    }

    // 
    let chatMessages;
    try {
      //  TavernHelper 
      if (typeof (window as any).TavernHelper !== 'undefined') {
        const lastMessageId = (window as any).TavernHelper.getLastMessageId?.() ?? 0;
        console.log('ID:', lastMessageId);

        if (typeof (window as any).TavernHelper.getChatMessages === 'function') {
          chatMessages = (window as any).TavernHelper.getChatMessages(`0-${lastMessageId}`);
          console.log('  TavernHelper :', chatMessages.length);
        } else {
          throw new Error('TavernHelper.getChatMessages ');
        }
      } else {
        throw new Error('TavernHelper ');
      }
    } catch (error) {
      console.error(':', error);
      window.toastr.error(': ' + (error as Error).message);
      return;
    }

    if (!chatMessages || chatMessages.length === 0) {
      window.toastr.warning('');
      return;
    }

    // 
    let hiddenCount = 0;
    const messagesToHide = [];

    for (const messageId of messageIds) {
      // 
      const alreadyHidden = hidden_messages.value.some(msg => msg.message_id === messageId);
      if (alreadyHidden) {
        console.log(` ${messageId} `);
        continue;
      }

      const message = chatMessages.find(msg => msg.message_id === messageId);
      if (message) {
        // 
        messagesToHide.push({
          message_id: messageId,
          is_hidden: true,
        });

        // 
        hidden_messages.value.push({
          message_id: messageId,
          name: message.name || 'Unknown',
          role: message.role || 'user',
          message: message.message || '',
        });
        hiddenCount++;
      }
    }

    // API
    if (messagesToHide.length > 0) {
      try {
        console.log(' slash :', messageIds);

        //  slash 
        for (const messageId of messageIds) {
          try {
            await triggerSlash(`/hide ${messageId}`);
            console.log(` ${messageId}`);
          } catch (error) {
            console.error(` ${messageId} :`, error);
          }
        }

        window.toastr.success('');
      } catch (error) {
        console.error('API:', error);
        window.toastr.error('API: ' + (error as Error).message);
      }
    }

    if (hiddenCount > 0) {
      // 
      saveHiddenMessages();
      window.toastr.success(` ${hiddenCount} `);
      hide_range.value = ''; // 
    } else {
      window.toastr.warning('');
    }
  } catch (error) {
    console.error(':', error);
    window.toastr.error(': ' + (error as Error).message);
  }
};

const handle_show_messages = async () => {
  try {
    console.log('...');

    if (!hide_range.value.trim()) {
      window.toastr.warning('');
      return;
    }

    // 
    const messageIds = parseMessageRange(hide_range.value.trim());
    if (messageIds.length === 0) {
      window.toastr.warning('');
      return;
    }

    // 
    let chatMessages;
    try {
      //  TavernHelper 
      if (typeof (window as any).TavernHelper !== 'undefined') {
        const lastMessageId = (window as any).TavernHelper.getLastMessageId?.() ?? 0;
        console.log('ID:', lastMessageId);

        if (typeof (window as any).TavernHelper.getChatMessages === 'function') {
          chatMessages = (window as any).TavernHelper.getChatMessages(`0-${lastMessageId}`);
          console.log('  TavernHelper :', chatMessages.length);
        } else {
          throw new Error('TavernHelper.getChatMessages ');
        }
      } else {
        throw new Error('TavernHelper ');
      }
    } catch (error) {
      console.error(':', error);
      window.toastr.error(': ' + (error as Error).message);
      return;
    }

    if (!chatMessages || chatMessages.length === 0) {
      window.toastr.warning('');
      return;
    }

    // 
    const messagesToShow = [];
    let shownCount = 0;

    for (const messageId of messageIds) {
      const message = chatMessages.find(msg => msg.message_id === messageId);
      if (message) {
        messagesToShow.push({
          message_id: messageId,
          is_hidden: false,
        });
        shownCount++;
      }
    }

    if (messagesToShow.length === 0) {
      window.toastr.warning('');
      return;
    }

    // API
    try {
      //  slash 
      for (const messageId of messageIds) {
        try {
          await triggerSlash(`/unhide ${messageId}`);
          console.log(` ${messageId}`);
        } catch (error) {
          console.error(` ${messageId} :`, error);
        }
      }
    } catch (error) {
      console.error('API:', error);
      window.toastr.error('API: ' + (error as Error).message);
      return;
    }

    // 
    hidden_messages.value = hidden_messages.value.filter(hiddenMsg => !messageIds.includes(hiddenMsg.message_id));

    // 
    saveHiddenMessages();

    window.toastr.success(` ${shownCount} `);
    hide_range.value = ''; // 
  } catch (error) {
    console.error(':', error);
    window.toastr.error(': ' + (error as Error).message);
  }
};

const handle_refresh_hidden = async (showToast: boolean = false) => {
  try {
    console.log('', showToast);

    // 
    if (hidden_messages.value.length === 0) {
      console.log('...');
      loadHiddenMessages();
    }

    // 
    let chatMessages;
    try {
      //  TavernHelper 
      if (typeof (window as any).TavernHelper !== 'undefined') {
        const lastMessageId = (window as any).TavernHelper.getLastMessageId?.() ?? 0;
        console.log('ID:', lastMessageId);

        if (typeof (window as any).TavernHelper.getChatMessages === 'function') {
          chatMessages = (window as any).TavernHelper.getChatMessages(`0-${lastMessageId}`);
          console.log('  TavernHelper :', chatMessages.length);
        } else {
          throw new Error('TavernHelper.getChatMessages ');
        }
      } else {
        throw new Error('TavernHelper ');
      }
    } catch (error) {
      console.error(':', error);
      if (showToast) {
        window.toastr.error(': ' + (error as Error).message);
      }
      return;
    }

    if (!chatMessages || chatMessages.length === 0) {
      if (showToast) {
        window.toastr.warning('');
      }
      return;
    }

    // 
    const validHiddenMessages = [];
    let removedCount = 0;

    for (const hiddenMsg of hidden_messages.value) {
      const message = chatMessages.find(msg => msg.message_id === hiddenMsg.message_id);
      if (message) {
        // 
        validHiddenMessages.push({
          message_id: hiddenMsg.message_id,
          name: message.name || hiddenMsg.name,
          role: message.role || hiddenMsg.role,
          message: message.message || hiddenMsg.message,
        });
      } else {
        // 
        removedCount++;
      }
    }

    // 
    hidden_messages.value = validHiddenMessages;

    // 
    saveHiddenMessages();

    if (showToast) {
      if (removedCount > 0) {
        window.toastr.success(` ${removedCount} `);
      } else {
        window.toastr.success('');
      }
    }

    console.log(`${validHiddenMessages.length} ${removedCount} `);
  } catch (error) {
    console.error(':', error);
    if (showToast) {
      window.toastr.error(': ' + (error as Error).message);
    }
  }
};

const handle_unhide_single = async (messageId: number) => {
  try {
    console.log('', messageId);

    // 
    const index = hidden_messages.value.findIndex(msg => msg.message_id === messageId);
    if (index === -1) {
      window.toastr.warning(` #${messageId} `);
      return;
    }

    // API
    try {
      //  slash 
      await triggerSlash(`/unhide ${messageId}`);
      console.log(':', messageId);
    } catch (error) {
      console.error('API:', error);
      window.toastr.warning('API');
      return;
    }

    // 
    hidden_messages.value.splice(index, 1);

    // 
    saveHiddenMessages();

    window.toastr.success(` #${messageId}`);
  } catch (error) {
    console.error(':', error);
    window.toastr.error(': ' + (error as Error).message);
  }
};
</script>

