      'script.js': `// 
let chatHistory = [];

//  localStorage 
function loadHistory() {
  try {
    const saved = localStorage.getItem('chat_history_demo');
    chatHistory = saved ? JSON.parse(saved) : [];
  } catch (e) {
    console.error(':', e);
    chatHistory = [];
  }
  renderMessages();
}

//  localStorage
function saveHistory() {
  try {
    localStorage.setItem('chat_history_demo', JSON.stringify(chatHistory));
  } catch (e) {
    console.error(':', e);
  }
}

// 
function renderMessages() {
  const list = document.getElementById('messageList');
  list.innerHTML = '';
  chatHistory.forEach(msg => {
    const div = document.createElement('div');
    div.className = 'message ' + msg.sender;
    div.textContent = msg.content;
    list.appendChild(div);
  });
  list.scrollTop = list.scrollHeight;
}

// 
async function sendMessage() {
  const input = document.getElementById('userInput');
  const text = input.value.trim();
  if (!text) return;

  // 
  chatHistory.push({
    id: Date.now(),
    sender: 'user',
    content: text,
    timestamp: new Date().toLocaleTimeString()
  });
  input.value = '';
  renderMessages();
  saveHistory();

  //  AI
  const prompt = '\\n' + chatHistory.map(m => m.sender + ': ' + m.content).join('\\n');

  let aiText = '';
  await generate({
    should_stream: true,
    prompt: prompt,
    on_stream_chunk: (chunk) => {
      aiText += chunk;
      // 
      const tempMsg = { id: 'temp', sender: 'assistant', content: aiText };
      const tempIndex = chatHistory.findIndex(m => m.id === 'temp');
      if (tempIndex >= 0) {
        chatHistory[tempIndex] = tempMsg;
      } else {
        chatHistory.push(tempMsg);
      }
      renderMessages();
    },
    on_stream_end: () => {
      // 
      const tempIndex = chatHistory.findIndex(m => m.id === 'temp');
      if (tempIndex >= 0) {
        chatHistory[tempIndex] = {
          id: Date.now(),
          sender: 'assistant',
          content: aiText,
          timestamp: new Date().toLocaleTimeString()
        };
      }
      renderMessages();
      saveHistory();
    }
  });
}

// 
$(() => {
  loadHistory();
  document.getElementById('sendBtn').addEventListener('click', sendMessage);
  document.getElementById('userInput').addEventListener('keydown', (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  });
});`,
