  const template = projectTemplates[templateKey as keyof typeof projectTemplates];
  if (!template) return;

  const name = prompt(':', template.name);
  if (!name || !name.trim()) return;

  const newProj: Project = {
    id: Date.now().toString(),
    name: name.trim(),
