function triggerFolderImport() {
  if (folderInputRef.value) {
    folderInputRef.value.value = ''; // 
    folderInputRef.value.click();
  }
}

async function handleFolderImport(event: Event) {
  const input = event.target as HTMLInputElement;
  const files = input.files;

  if (!files || files.length === 0) {
    return;
  }

  try {
    // 
    const firstFile = files[0];
    const pathParts = firstFile.webkitRelativePath.split('/');
    const folderName = pathParts[0] || '';

    // 
    const projectName = prompt(':', folderName);
    if (!projectName || !projectName.trim()) {
      return;
    }

    // 
    const supportedExtensions = ['.html', '.css', '.js', '.ts', '.scss', '.sass', '.json', '.txt', '.md'];

    // 
    const projectFiles: ProjectFile[] = [];
    const fileReadPromises: Promise<void>[] = [];

    for (let i = 0; i < files.length; i++) {
      const file = files[i];
      const relativePath = file.webkitRelativePath.split('/').slice(1).join('/'); // 

      // /
      if (!relativePath || relativePath.startsWith('.') || relativePath.includes('/.')) {
        continue;
      }

      //  node_modulesdist 
      if (
        relativePath.includes('node_modules/') ||
        relativePath.includes('dist/') ||
        relativePath.includes('.git/') ||
        relativePath.includes('build/')
      ) {
        continue;
      }

      // 
      const extension = '.' + relativePath.split('.').pop()?.toLowerCase();
      if (!supportedExtensions.includes(extension)) {
        continue;
      }

      // 
      const readPromise = new Promise<void>((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = e => {
          const content = e.target?.result as string;
          const fileName = relativePath.split('/').pop() || relativePath;
          projectFiles.push({
            path: relativePath,
            name: fileName,
            content: content || '',
          });
          resolve();
        };
        reader.onerror = () => reject(new Error(`: ${relativePath}`));
        reader.readAsText(file);
      });

      fileReadPromises.push(readPromise);
    }

    // 
    await Promise.all(fileReadPromises);

    if (projectFiles.length === 0) {
      window.toastr.warning('HTMLCSSJSTSSCSS ');
      return;
    }

    // 
    const newProj: Project = {
      id: Date.now().toString(),
      name: projectName.trim(),
      files: projectFiles,
    };

    projects.value.push(newProj);
    currentId.value = newProj.id;
    currentFile.value = newProj.files[0]?.path || '';
    code.value = newProj.files[0]?.content || '';

    saveToChatVar();
    updatePreview();

    window.toastr.success(`"${projectName}" ${projectFiles.length} `);
  } catch (error) {
    console.error(':', error);
    window.toastr.error(': ' + (error as Error).message);
  }
}

