function createNewFolder() {
  const proj = currentProject.value;
  if (!proj) return;

  const folderPath = prompt('\ncss  js/utils', '');
  if (!folderPath || !folderPath.trim()) return;

  const trimmedPath = folderPath.trim().replace(/\/$/, ''); // 

  //  .gitkeep 
  const gitkeepPath = `${trimmedPath}/.gitkeep`;

  // 
  if (proj.files.some(f => f.path.startsWith(`${trimmedPath}/`))) {
    window.toastr.warning(` "${trimmedPath}" `);
    return;
  }

  // 
  const newFile: ProjectFile = {
    path: gitkeepPath,
    name: '.gitkeep',
    content: '# \n',
  };

  proj.files.push(newFile);
  saveToChatVar();

  window.toastr.success(` "${trimmedPath}" `);
}

