<script setup lang="ts">
import { watchDebounced } from '@vueuse/core';
import { klona } from 'klona';
import { storeToRefs } from 'pinia';
import { onMounted, ref } from 'vue';
import { normalizeApiEndpoint, useSettingsStore } from '../settings';
import { useTaskStore } from '../taskStore';
import { copyToClipboard, getScriptIdSafe } from '../utils';

const settingsStore = useSettingsStore();
const { settings } = storeToRefs(settingsStore);
const taskStore = useTaskStore();

const triggerKeyword = ref('');
const interfaceDescription = ref('');
const generatedCode = ref('');
const generatedRegex = ref('');
const isGenerating = ref(false);
const isModifying = ref(false);
const showCode = ref(false);
const showModify = ref(false);
const modifyInstruction = ref('');
const previewFrame = ref<HTMLIFrameElement | null>(null);

//  localStorage 
const loadData = () => {
  try {
    const scriptId = getScriptIdSafe();
    if (!scriptId) return;

    const storageKey = `${scriptId}_regex_ui_generator`;
    const savedDataString = localStorage.getItem(storageKey);

    if (savedDataString) {
      try {
        const savedData = JSON.parse(savedDataString);
        triggerKeyword.value = savedData.triggerKeyword || '';
        interfaceDescription.value = savedData.interfaceDescription || '';
        generatedCode.value = savedData.generatedCode || '';
        generatedRegex.value = savedData.generatedRegex || '';
        console.log(' []  localStorage ');
      } catch (parseError) {
        console.error(' [] :', parseError);
      }
    }
  } catch (error) {
    console.error(' [] :', error);
  }
};

//  localStorage
const saveData = () => {
  try {
    const scriptId = getScriptIdSafe();
    if (!scriptId) return;

    const dataToSave = {
      triggerKeyword: triggerKeyword.value,
      interfaceDescription: interfaceDescription.value,
      generatedCode: generatedCode.value,
      generatedRegex: generatedRegex.value,
    };

    const storageKey = `${scriptId}_regex_ui_generator`;
    localStorage.setItem(storageKey, JSON.stringify(dataToSave));
    console.log(' []  localStorage');
  } catch (error) {
    console.error(' [] :', error);
  }
};

// 
onMounted(() => {
  loadData();
});

// 
// 
watchDebounced(
  [triggerKeyword, interfaceDescription, generatedCode, generatedRegex],
  () => {
    saveData();
  },
  { debounce: 500 },
);

// AI 
const generateWithAI = async () => {
  if (!triggerKeyword.value || !interfaceDescription.value) {
    window.toastr.warning('');
    return;
  }

  // 
  const taskId = taskStore.createTask('ui_generate', `${triggerKeyword.value}`);
  isGenerating.value = true;

  try {
    taskStore.updateTaskProgress(taskId, 10, '...');

    const systemPrompt = ` HTML 

# 
**** HTML  CSS  JS

# 
1. **HTML** \`<!DOCTYPE html>\`  \`</html>\` 
2. **** \`<style>\` CSS
3. **** \`<script>\` JS
4. **** CDN 

# 
1. ****
2. **** CSS flexbox, grid, 
3. ****
4. ****

# 
 
  markdown \`\`\`html
 
 "......"
 

# 
 HTML  \`<!DOCTYPE html>\` `;

    const userPrompt = `****${triggerKeyword.value}

****
${interfaceDescription.value}

---

****
1.  HTML <style>  <script>
2. ${interfaceDescription.value.includes('') ? '' : ''}
3. 
4. 

HTML`;

    taskStore.updateTaskProgress(taskId, 20, ' AI ...');
    taskStore.addTaskDetail(taskId, `: ${triggerKeyword.value}`);

    const apiUrl = normalizeApiEndpoint(settings.value.api_endpoint);

    const requestParams = {
      model: settings.value.model,
      messages: [
        {
          role: 'system',
          content: systemPrompt,
        },
        { role: 'user', content: userPrompt },
      ],
      temperature: settings.value.temperature || 0.7,
      max_tokens: settings.value.max_tokens || 16000,
      top_p: settings.value.top_p,
      presence_penalty: settings.value.presence_penalty,
      frequency_penalty: settings.value.frequency_penalty,
    };

    // 
    const { filterApiParams } = await import('../settings');
    const filteredParams = filterApiParams(requestParams, settings.value.api_endpoint);

    const response = await fetch(apiUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${settings.value.api_key}`,
      },
      body: JSON.stringify(filteredParams),
    });

    taskStore.updateTaskProgress(taskId, 40, 'AI...');

    if (!response.ok) {
      throw new Error(`API : ${response.status}`);
    }

    taskStore.updateTaskProgress(taskId, 50, ' AI ...');
    taskStore.addTaskDetail(taskId, 'AI HTML...');

    const data = await response.json();
    let result = data.choices?.[0]?.message?.content || '';

    taskStore.updateTaskProgress(taskId, 70, ' AI ...');

    console.log(' [] AI :', result.length);
    console.log(' [] AI 500:', result.substring(0, 500));

    //  markdown 
    result = result
      .replace(/```html\n?/g, '')
      .replace(/```\n?/g, '')
      .trim();

    // HTMLAI
    // 1.  <!DOCTYPE html 
    const doctypeMatch = result.match(/<!DOCTYPE html>[\s\S]*/i);
    if (doctypeMatch) {
      result = doctypeMatch[0].trim();
      console.log(' [] HTMLDOCTYPE');
    } else {
      // 2. DOCTYPE <html 
      const htmlMatch = result.match(/<html[\s\S]*/i);
      if (htmlMatch) {
        result = htmlMatch[0].trim();
        console.log(' [] HTMLDOCTYPE');
      } else if (result.includes('<') && result.includes('>')) {
        // 3. htmlHTML
        console.log(' [] HTML');
        // 
        if (!result.includes('<html')) {
          result = `<!DOCTYPE html>
<html>
<head>
  <meta charset="UTF-8">
  <meta name="viewport" content="width=device-width, initial-scale=1.0">
</head>
<body>
${result}
</body>
</html>`;
          console.log(' [] HTML');
        }
      } else {
        // 4. HTMLAI
        console.error(' [] AIHTML');
        console.error('AI:', result);
        throw new Error('AIHTML');
      }
    }

    console.log(' [] HTML:', result.length);

    taskStore.updateTaskProgress(taskId, 85, '...');

    generatedCode.value = result;

    // 
    const findRegex = triggerKeyword.value.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
    const regexJson = {
      id: `regex_ui_${Date.now()}`,
      scriptName: `_${triggerKeyword.value}`,
      findRegex: findRegex,
      replaceString: result,
      trimStrings: [],
      placement: [2], // AI
      disabled: false,
      markdownOnly: true,
      promptOnly: false,
      runOnEdit: true,
      substituteRegex: 0,
      minDepth: null,
      maxDepth: null,
    };
    generatedRegex.value = JSON.stringify(regexJson, null, 2);

    taskStore.addTaskDetail(taskId, ` HTML: ${result.length} `);
    taskStore.completeTask(taskId, { code: result, regex: generatedRegex.value });

    window.toastr.success('AI ');
  } catch (error) {
    console.error('AI :', error);
    taskStore.failTask(taskId, (error as Error).message);
    window.toastr.error('AI : ' + (error as Error).message);
  } finally {
    isGenerating.value = false;
  }
};

// 
const showModifyDialog = () => {
  modifyInstruction.value = '';
  showModify.value = true;
};

// AI 
const modifyWithAI = async () => {
  if (!modifyInstruction.value) {
    window.toastr.warning('');
    return;
  }

  // 
  const taskId = taskStore.createTask('ui_modify', `${triggerKeyword.value}`);
  isModifying.value = true;

  try {
    taskStore.updateTaskProgress(taskId, 10, '...');

    const systemPrompt = `

# 
**** HTML 

# 
1. ****
2. ****
3. ****
4. ****

# 
 
  markdown \`\`\`html
 
 

# 
 HTML  \`<!DOCTYPE html>\` `;

    const userPrompt = `****${triggerKeyword.value}

****
${interfaceDescription.value}

****
${modifyInstruction.value}

---

****
1. 
2. 
3. 

HTML`;

    taskStore.updateTaskProgress(taskId, 20, '...');
    taskStore.addTaskDetail(taskId, `: ${modifyInstruction.value}`);

    const apiUrl = normalizeApiEndpoint(settings.value.api_endpoint);

    const response = await fetch(apiUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${settings.value.api_key}`,
      },
      body: JSON.stringify({
        model: settings.value.model,
        messages: [
          {
            role: 'system',
            content: systemPrompt,
          },
          { role: 'user', content: userPrompt },
        ],
        temperature: settings.value.temperature || 0.7,
        max_tokens: settings.value.max_tokens || 16000,
        top_p: settings.value.top_p,
        presence_penalty: settings.value.presence_penalty,
        frequency_penalty: settings.value.frequency_penalty,
      }),
    });

    taskStore.updateTaskProgress(taskId, 40, 'AI...');

    if (!response.ok) {
      throw new Error(`API : ${response.status}`);
    }

    taskStore.updateTaskProgress(taskId, 50, ' AI ...');
    taskStore.addTaskDetail(taskId, 'AI ...');

    const data = await response.json();
    let result = data.choices?.[0]?.message?.content || '';

    taskStore.updateTaskProgress(taskId, 70, '...');

    console.log(' [] AI :', result.length);
    console.log(' [] AI 500:', result.substring(0, 500));

    //  markdown 
    result = result
      .replace(/```html\n?/g, '')
      .replace(/```\n?/g, '')
      .trim();

    // HTMLAI
    // 1.  <!DOCTYPE html 
    const doctypeMatch = result.match(/<!DOCTYPE html>[\s\S]*/i);
    if (doctypeMatch) {
      result = doctypeMatch[0].trim();
      console.log(' [] HTMLDOCTYPE');
    } else {
      // 2. DOCTYPE <html 
      const htmlMatch = result.match(/<html[\s\S]*/i);
      if (htmlMatch) {
        result = htmlMatch[0].trim();
        console.log(' [] HTMLDOCTYPE');
      } else if (result.includes('<') && result.includes('>')) {
        // 3. htmlHTML
        console.log(' [] HTML');
        // 
        if (!result.includes('<html')) {
          result = `<!DOCTYPE html>
<html>
<head>
  <meta charset="UTF-8">
  <meta name="viewport" content="width=device-width, initial-scale=1.0">
</head>
<body>
${result}
</body>
</html>`;
          console.log(' [] HTML');
        }
      } else {
        // 4. HTMLAI
        console.error(' [] AIHTML');
        console.error('AI:', result);
        throw new Error('AIHTML');
      }
    }

    console.log(' [] HTML:', result.length);

    taskStore.updateTaskProgress(taskId, 85, '...');

    generatedCode.value = result;

    // 
    const findRegex = triggerKeyword.value.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
    const regexJson = {
      id: `regex_ui_${Date.now()}`,
      scriptName: `_${triggerKeyword.value}`,
      findRegex: findRegex,
      replaceString: result,
      trimStrings: [],
      placement: [2], // AI
      disabled: false,
      markdownOnly: true,
      promptOnly: false,
      runOnEdit: true,
      substituteRegex: 0,
      minDepth: null,
      maxDepth: null,
    };
    generatedRegex.value = JSON.stringify(regexJson, null, 2);

    // 
    interfaceDescription.value = `${interfaceDescription.value}\n\n\n${modifyInstruction.value}`;

    taskStore.addTaskDetail(taskId, ` : ${result.length} `);
    taskStore.completeTask(taskId, { code: result, regex: generatedRegex.value });

    window.toastr.success('AI ');
    showModify.value = false;
  } catch (error) {
    console.error('AI :', error);
    taskStore.failTask(taskId, (error as Error).message);
    window.toastr.error('AI : ' + (error as Error).message);
  } finally {
    isModifying.value = false;
  }
};

// 
const copyRegex = () => {
  copyToClipboard(generatedRegex.value, '');
};
</script>

