// 
function createMinimizeIcon() {
  // 
  const existingIcon = $('#memoryPanelMinimizeIcon');
  if (existingIcon.length > 0) {
    existingIcon.fadeIn(200);
    return existingIcon;
  }

  const isMobile = window.innerWidth <= 768;

  // 
  const tabs = [
    { key: 'settings', label: ' ', icon: 'fa-solid fa-cog' },
    { key: 'summary', label: ' ', icon: 'fa-solid fa-list' },
    { key: 'table', label: ' ', icon: 'fa-solid fa-table' },
    { key: 'greetings', label: ' ', icon: 'fa-solid fa-comments' },
    { key: 'regex', label: ' ', icon: 'fa-solid fa-code' },
    { key: 'status', label: ' ', icon: 'fa-solid fa-chart-bar' },
    { key: 'project', label: ' ', icon: 'fa-solid fa-laptop-code' },
    { key: 'tools', label: ' ', icon: 'fa-solid fa-tools' },
    { key: 'mvu', label: ' MVU Beta', icon: 'fa-solid fa-flask' },
    { key: 'help', label: ' ', icon: 'fa-solid fa-question-circle' },
  ];

  // 
  const quickMenu = $(`
    <div id="memoryPanelQuickMenu" style="
      position: fixed;
      top: 0;
      left: 0;
      background: #2a2a2a;
      border: 2px solid #4a9eff;
      border-radius: 12px;
      padding: 8px;
      display: none;
      z-index: 999999;
      box-shadow: 0 8px 32px rgba(74, 158, 255, 0.6), 0 4px 16px rgba(0, 0, 0, 0.8);
      min-width: 200px;
      max-height: 80vh;
      overflow-y: auto;
    ">
      <div style="
        padding: 8px 12px;
        color: #4a9eff;
        font-size: 12px;
        font-weight: 600;
        border-bottom: 1px solid rgba(74, 158, 255, 0.2);
        margin-bottom: 4px;
      ">
        
      </div>
      ${tabs
        .map(
          tab => `
        <div class="quick-menu-item" data-tab="${tab.key}" style="
          padding: 10px 12px;
          margin: 2px 0;
          cursor: pointer;
          border-radius: 8px;
          transition: all 0.2s;
          color: #e0e0e0;
          font-size: 13px;
          display: flex;
          align-items: center;
          gap: 8px;
          background: transparent;
        ">
          ${tab.label}
        </div>
      `,
        )
        .join('')}
    </div>
  `);

  // 
  quickMenu.find('.quick-menu-item').hover(
    function () {
      $(this).css({
        background: 'rgba(74, 158, 255, 0.15)',
        color: '#4a9eff',
        transform: 'translateX(-3px)',
      });
    },
    function () {
      $(this).css({
        background: 'transparent',
        color: '#e0e0e0',
        transform: 'translateX(0)',
      });
    },
  );

  // 
  quickMenu.find('.quick-menu-item').on('click', function () {
    const tabKey = $(this).data('tab');
    const panel = $('#memoryManagementPanel');

    // 
    if (!panel.is(':visible')) {
      panel.fadeIn(200);
    }

    // 
    setTimeout(() => {
      const tabOrder = [
        'settings',
        'summary',
        'table',
        'greetings',
        'regex',
        'status',
        'project',
        'tools',
        'mvu',
        'help',
      ];
      const tabIndex = tabOrder.indexOf(tabKey);

      if (tabIndex >= 0) {
        // 
        const tabs = panel.find('.tab-item');
        if (tabs.length > tabIndex) {
          tabs.eq(tabIndex).trigger('click');
        }
      }
    }, 150);

    // 
    quickMenu.fadeOut(200);
  });

  // 
  quickMenu.css({
    'scrollbar-width': 'thin',
    'scrollbar-color': '#4a9eff #1a1a1a',
  });

  const icon = $(`
    <div id="memoryPanelMinimizeIcon" style="
      position: fixed;
      top: ${isMobile ? '20px' : '20px'};
      right: ${isMobile ? '20px' : '20px'};
      width: ${isMobile ? '64px' : '72px'};
      height: ${isMobile ? '64px' : '72px'};
      background: linear-gradient(135deg, #2a2a2a 0%, #1f1f1f 100%);
      border: 3px solid #4a9eff;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      z-index: 999998;
      box-shadow: 0 4px 16px rgba(74, 158, 255, 0.4), 0 2px 8px rgba(0, 0, 0, 0.3);
      transition: all 0.3s ease;
      font-size: ${isMobile ? '32px' : '38px'};
    ">
      
    </div>
  `);

  let hideMenuTimeout: number | null = null;

  // 
  function updateMenuPosition() {
    const iconPos = icon.offset();
    const iconWidth = icon.outerWidth() || 72;
    const iconHeight = icon.outerHeight() || 72;
    const menuWidth = quickMenu.outerWidth() || 200;
    const menuHeight = quickMenu.outerHeight() || 400;

    console.log(' :', { iconPos, iconWidth, iconHeight, menuWidth, menuHeight });

    if (!iconPos) {
      console.warn(' ');
      return;
    }

    // 
    let left = iconPos.left - menuWidth - 10;
    let top = iconPos.top;

    console.log(' :', { left, top });

    // 
    if (left < 10) {
      left = iconPos.left + iconWidth + 10;
      console.log(' :', left);
    }

    // 
    const windowWidth = $(window).width() || 1920;
    if (left + menuWidth > windowWidth - 10) {
      left = iconPos.left - (menuWidth - iconWidth) / 2;
      // 
      if (iconPos.top > menuHeight + 10) {
        top = iconPos.top - menuHeight - 10;
        console.log(' :', { left, top });
      } else {
        // 
        top = iconPos.top + iconHeight + 10;
        console.log(' :', { left, top });
      }
    }

    // 
    const windowHeight = $(window).height() || 1080;
    const finalLeft = Math.max(10, Math.min(left, windowWidth - menuWidth - 10));
    const finalTop = Math.max(10, Math.min(top, windowHeight - Math.min(menuHeight, windowHeight * 0.8) - 10));

    console.log(' :', { finalLeft, finalTop, windowWidth, windowHeight });

    quickMenu.css({ left: finalLeft + 'px', top: finalTop + 'px' });
  }

  //  + 
  icon.hover(
    function () {
      if (hideMenuTimeout) {
        clearTimeout(hideMenuTimeout);
        hideMenuTimeout = null;
      }

      $(this).css({
        transform: 'scale(1.15) rotate(5deg)',
        boxShadow: '0 6px 24px rgba(74, 158, 255, 0.6), 0 4px 12px rgba(0, 0, 0, 0.4)',
        borderColor: '#5ab0ff',
      });

      // 
      quickMenu.css('opacity', '0').show();
      updateMenuPosition();
      quickMenu.css('opacity', '1').hide().fadeIn(200);
      console.log(' :', quickMenu.offset());
    },
    function () {
      $(this).css({
        transform: 'scale(1) rotate(0deg)',
        boxShadow: '0 4px 16px rgba(74, 158, 255, 0.4), 0 2px 8px rgba(0, 0, 0, 0.3)',
        borderColor: '#4a9eff',
      });

      // 
      hideMenuTimeout = window.setTimeout(() => {
        if (!quickMenu.is(':hover')) {
          quickMenu.fadeOut(200);
        }
      }, 200);
    },
  );

  // 
  quickMenu.hover(
    function () {
      if (hideMenuTimeout) {
        clearTimeout(hideMenuTimeout);
        hideMenuTimeout = null;
      }
    },
    function () {
      quickMenu.fadeOut(200);
    },
  );

  // 
  icon.on('click', () => {
    const panel = $('#memoryManagementPanel');
    if (panel.is(':visible')) {
      // 
      panel.fadeOut(200);
      icon.fadeIn(200);
    } else {
      // 
      panel.fadeIn(200);
    }
  });

  // 
  icon.draggable({
    containment: 'window',
    scroll: false,
    start: function () {
      $(this).css('transition', 'none');
      quickMenu.hide(); // 
    },
    stop: function () {
      $(this).css('transition', 'all 0.3s ease');
    },
  });

  $('body').append(quickMenu);
  $('body').append(icon);

  console.log(' :', icon);
  console.log(' :', quickMenu);
  console.log(' :', $('#memoryPanelQuickMenu').length > 0);

  return icon;
}

// 
export function minimizeMemoryPanel() {
  const panel = $('#memoryManagementPanel');
  if (panel.length === 0) return;

  panel.fadeOut(200);
  createMinimizeIcon(); // 
}

// /
export function toggleMemoryPanel() {
  const panel = $('#memoryManagementPanel');
  if (panel.length === 0) return;

  if (panel.is(':visible')) {
    panel.fadeOut(200);
  } else {
    panel.fadeIn(200);
  }
}
